.dog-card {
  background-color: rgba(255, 255, 255, 0.8);
  width: 100%;
  display: flex;
  border-radius: 11px;
  overflow: hidden;
  position: relative;
}

.card-img {
  width: 40rem;
}

.card-text-title {
  font-size: 2rem;
  font-weight: 600;
  padding: 3.2rem;
}

.card-text {
  font-size: 1.6rem;
  font-weight: 500;
  padding: 0 3.2rem;
  margin-bottom: 3.2rem;
  line-height: 1.6;
}
