.button-icon {
  background-color: #cd853f;
  padding: 1.2rem;
  font-size: 2rem;
  color: #ffffff;
}

.fas {
  transform: rotate(-45deg);
}

.button-box {
  margin: 0 auto;
  background-color: #ffffff;
  width: fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 11px;
  overflow: hidden;
}

.button-box:hover {
  cursor: pointer;
  background-color: #cd853f;
}

.button-box.disabled:hover {
  background-color: #fff;
}

.button-box:hover > .button-icon {
  background-color: #ffffff;
  color: #000;
}

.button-box:hover > .button {
  color: #fff;
}

.button {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 20rem;
  font-size: 2rem;
  font-weight: 700;
}

.disabled {
  pointer-events: none;
}

.disabled > .button-icon {
  background-color: #999999;
  color: #000;
  cursor: not-allowed;
}

.disabled > .button {
  color: #999999;
  cursor: not-allowed;
}

.disabled:hover > .button-icon {
  background-color: #999999;
  color: #000;
  cursor: not-allowed;
}

.disabled:hover > .button {
  background-color: #fff;
  color: #999999;
  cursor: not-allowed;
}

.disabled .fas {
  animation-name: spin;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
