/* 1 rem = 1 em = 16px */

/* 1200px */
@media (max-width: 75em) {
  html {
    font-size: 50%;
  }

  .MainBody {
    width: 100rem;
  }

  .dog-window {
    width: fit-content;
  }

  .dog-card {
    flex-direction: column;
    width: fit-content;
    align-items: center;
  }

  .card-img {
    width: 55rem;
  }

  .vote-arrows {
    color: #cd853f;
  }
}

@media (max-width: 52em) {
  html {
    font-size: 30%;
  }
}

@media (max-width: 32em) {
  .MainBody {
    width: fit-content;
    flex-direction: column;
    align-items: center;
  }
} ;
