.footer {
  width: 100vw;
  background-color: #333;
}

.footer-content {
  margin: 0 auto;
  padding: 1.8rem 3.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-txt-big {
  font-size: 1.6rem;
  color: #fff;
}

.footer-txt {
  font-size: 1.2rem;
  color: #fff;
}

.version {
  font-weight: 700;
}

.link:link,
.link:visited {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  transition: all 0.3s;
}

.link:hover,
.link:active {
  color: #fff;
  border-bottom: 1px solid transparent;
}
