.vote-arrows {
  width: fit-content;
  padding: 1.2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 3.2rem;
  text-align: center;
  position: absolute;
  right: 0;
  color: #777777;
  cursor: pointer;
}

.fa-caret-down {
  transition: all 300ms;
}

.fa-caret-down:hover {
  color: peru;
  transform: scale(1.5);
}

.fa-caret-up {
  transition: all 300ms;
}

.fa-caret-up:hover {
  color: peru;
  transform: scale(1.5);
}
