/*

----- Typography system -----
Font size system (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

----- Whitespace -----
Spacing system (px)
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128

*/

.heading-primary,
.heading-secondary,
.heading-tertiary {
  font-weight: 700;
  color: #222;
  letter-spacing: -0.5px;
}

.heading-primary {
  font-size: 5.2rem;
  line-height: 1.2;
  letter-spacing: -3px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 2.4rem;
}

.heading-secondary {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 2.4rem;
}
