.header {
  width: auto;
  display: flex;
  justify-content: right;
  align-items: center;
  background-color: rgba(101, 184, 250, 0.6);
  height: 8rem;
  padding: 4.8rem;
  margin-bottom: 8.2rem;
}

.main-nav-list {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 4.8rem;
}

.main-nav-link:link,
.main-nav-link:visited {
  display: inline-block;
  text-decoration: none;
  color: #555555;
  font-weight: 600;
  font-size: 1.8rem;
}

.main-nav-link:hover,
.main-nav-link:active {
  border-bottom: 2px solid #cd853f;
}
